.App {
  text-align: center;
}

li{
  list-style: none;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 1000px;
  margin: 0 auto;
  height: 100vh;
}

.steps {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 0;
}

.step {
  width: 100px;
  height: 100px;
  border: 2px solid #633700;
  background-color: #00e04b;
  background-image: url("");
}

.start,.img {
  width: 100px;
  height: 100px;
  pointer-events: none;
}

.btn {
  padding: 5px;
  border: 2px solid red;
  cursor: pointer;
}


.arrows {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}
